<template>
  <div class="goods">
    <img class="goods-pic" :src="goods.prodImg" />
    <van-image :src="goods.img" />
    <van-cell-group>
      <van-cell :border="false">
        <div class="goods-title">{{ goods.prodName }}</div>
        <div class="goods-price">{{goods.unitPoints}}+{{goods.unitPrice}}</div>
      </van-cell>
      <van-cell class="goods-express">
        <van-col span="10">{{ goods.sales }}</van-col>
        <van-col span="14">{{ goods.stock }}</van-col>
      </van-cell>
      <van-cell class="goods-num">
        <van-stepper
          class="goods-buy-num"
          v-model="buyNum"
          integer
          min="1"
          max="9"
        />
      </van-cell>
    </van-cell-group>
    <van-cell-group class="goods-cell-group">
      <van-cell title="" />
      <van-cell>
        <div v-html="goods.intro" class="goods-des-info"></div>
      </van-cell>
    </van-cell-group>
    <van-goods-action>
      <van-goods-action-icon></van-goods-action-icon>
     <!-- <van-goods-action-icon icon="cart-o" @click="onClickCart">
        
      </van-goods-action-icon> -->
      <van-goods-action-icon></van-goods-action-icon>

      <van-goods-action-button type="warning" @click="onJoinCart">
        
      </van-goods-action-button>
      <van-goods-action-button type="danger" @click="onClickBuy">
        
      </van-goods-action-button>
    </van-goods-action>
  </div>
</template>

<script>
import {
  Tag,
  Col,
  Icon,
  Cell,
  CellGroup,
  Swipe,
  Image,
  SwipeItem,
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton,
  Stepper
} from "vant";
import { cart } from "@/mix/cart";

export default {
  name: "Goods",

  mixins: [cart],

  components: {
    [Tag.name]: Tag,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [Image.name]: Image,
    [CellGroup.name]: CellGroup,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [GoodsAction.name]: GoodsAction,
    [GoodsActionIcon.name]: GoodsActionIcon,
    [GoodsActionButton.name]: GoodsActionButton,
    [Stepper.name]: Stepper
  },

  data() {
    return {
      prodId:"",
      buyNum: 1,
      goods: {},
    };
  },

  async created(){
    this.prodId= this.$route.query.prodId
    const query = this.$gql`
      query mall($prodId: String!) {
        mall {
          product{
            info(
              prodId: $prodId
            ) {
                intro
                prodId
                prodImg
                prodName
                sales
                sortId
                sortName
                status
                stock
                unitPrice
                unitPoints
            }
          }
        }
      }
    `;
    const result = await this.$apollo.query({
      query: query,
      variables: {
        prodId: this.prodId
      }
    });
    let {
      data:{
        mall:{
          product:{
            info
          }
        }
      }
    } = result
    this.goods = info
    console.log(result)
  },

  methods: {
    onClickCart() {
      this.$router.push("cart");
    },

    onJoinCart() {
      const newItem = {
        unitPrice: this.goods.unitPrice,
        unitPoints: this.goods.unitPoints,
        num: this.buyNum,
        prodId: this.goods.prodId,
        prodName: this.goods.prodName,
        img: this.goods.img
      };
      this.joinCartData(newItem);
      this.$toast.success("");
    },

    onClickBuy() {
      if(this.judgeSignIn()){
        let item = this.goods
        let payPoints = item.unitPoints * this.buyNum
        let payAmount = item.unitPrice * this.buyNum
        this.createOrder({
              "unitPrice":item.unitPrice,
              "unitPoints":item.unitPoints,
              "num":this.buyNum,
              "prodId":item.prodId,
              "prodName":item.prodName
            },payPoints,payAmount);
      }
    }
  }
};
</script>

