import { comData } from "@/mix/comData";
export const cart = {
    mixins: [comData],
    methods: {
        readCartData() {
            const cartSession = sessionStorage.getItem(this.cartKey);
            if (cartSession) {
                return JSON.parse(cartSession);
            } else {
                return []
            }
        },
        joinCartData(newItem) {
            const cart = this.readCartData();
            const id = newItem.prodId
            if (cart.length > 0) {
                const index = cart.findIndex((value) => {
                    return value.prodId == id
                })
                console.log('index---', index)
                if (index == -1) {
                    cart.push(newItem)
                } else {
                    newItem.num += cart[index].num;
                    cart[index] = newItem
                }
            } else {
                cart.push(newItem);
            }
            sessionStorage.setItem(this.cartKey, JSON.stringify(cart));
        },
        async createOrder(details,payPoints,payAmount){
            const mutation = this.$gql`
            mutation createOrder($mallId: String!,
                $uid: String!,
                $payPoints: Int,
                $details: [OrderDetailsTypeArgs]!,
                $dedup: String!,
                $payAmount: Int,
                $redirectUrl: String) {
              mall {
                order{
                  create(
                    mallId: $mallId
                    uid:$uid
                    payPoints:$payPoints
                    payAmount:$payAmount
                    details:$details
                    dedup:$dedup,
                    redirectUrl:$redirectUrl
                  ) {
                    orderId
                    payUrl
                  }
                }
              }
            }
          `;
          const result = await this.$apollo.mutate({
            mutation: mutation,
            variables: {
              mallId:this.mallId,
              uid:this.uid,
              dedup:this.$util.guid(),
              details,
              payAmount,
              payPoints,
              redirectUrl:"http://www.baidu.com"
            }
          });
          let {
            data:{
              mall:{
                order:{
                    create:{
                        orderId,
                        payUrl
                    }
                }
              }
            }
          } = result
          window.location.href = payUrl
          console.log(orderId,payUrl)
        },
        judgeSignIn(){
            if(!this.token){
                this.$router.push({
                    path: "/login",
                    qurey:{mallId:this.mallId}
                });
                return false
            }else{
                return true
            }

        }
    },
}